
















































































































































































































































import {
  Vue,
  Component,
  Prop,
  InjectReactive,
  Watch,
  Ref
} from "vue-property-decorator";
import { TableData, ColumnType, TableColumn } from "../models/TableData";
import { ScriptItem, ScriptType } from "../models/ScriptItem";
import { State, Mutation } from "vuex-class";
import EditScriptItem from "./forms/EditScriptItem.vue";
import ScriptEditor from "./forms/ScriptEditor.vue";
import FlexibleInput from "./forms/FlexibleInput.vue";
import { QVirtualScroll, QVueGlobals } from "quasar";
import { PlayerState } from "../models/PlayerState";
import ScriptService from "../services/ScriptService";
import { AppSettings, KeyCombination } from "../models/AppSettings";

@Component({
  components: {
    EditScriptItem,
    FlexibleInput
  }
})
export default class ScriptTable extends ScriptEditor {
  @Prop({ default: false, type: Boolean }) disabled: Boolean | undefined;
  @State("lang") lang: any;
  @State("script") script: Array<any> | undefined;
  @State("settings") settings!: AppSettings;
  @Mutation("setScript") setScript: any;
  @Mutation("setPlayerState") setPlayerState: any;
  @State("player") playerState!: PlayerState;
  @Ref("editModal") editModal!: EditScriptItem;
  @Ref("scriptTableRef") scriptTable!: QVirtualScroll;
  // table: TableData<ScriptItem> | null = null;
  list: ScriptItem[] = [];
  selectedItems: any[] = [];
  columnType = ColumnType;
  scriptType = ScriptType;
  pagination: any = {
    rowsPerPage: 0
  };
  currentScript: number = 0;
  dragItem?: ScriptItem;
  lastDragIndex: number = -1;
  selectedItem!: ScriptItem;
  isPlaying = false;
  $q!: QVueGlobals;
  mounted() {
    (window as any)["addItem"] = (json: string) => {
      let item = JSON.parse(json) as ScriptItem;
      if (item) {
        if (this.list.length > 0) {
          let last = this.list[this.list.length - 1];
          if (
            last.eventType == ScriptType.KEY_DOWN &&
            item.eventType == ScriptType.KEY_UP &&
            (item.timeOffset == null || item.timeOffset <= 200) &&
            last.keyName == item.keyName
          ) {
            last.eventType = ScriptType.KEY_PRESS;
            this.list.splice(this.list.length - 1, 1, last);
            return;
          }
          if (
            last.eventType == ScriptType.MOUSE_DOWN &&
            item.eventType == ScriptType.MOUSE_UP &&
            (item.timeOffset == null || item.timeOffset <= 200) &&
            last.keyName == item.keyName
          ) {
            if (last.coord && item.coord) {
              let x = last.coord.x || 0;
              let y = last.coord.y || 0;
              let tx = item.coord.x || 0;
              let ty = item.coord.y || 0;
              if (Math.abs(x - tx) <= 20 && Math.abs(y - ty) <= 20) {
                last.eventType = ScriptType.MOUSE_CLICK;
                this.list.splice(this.list.length - 1, 1, last);
                return;
              }
            }
          }
        }
        this.list.push(item);
        this.scriptTable.scrollTo(this.list.length - 1);
        let recordHotkey = this.checkHotkey(this.settings.recordHotkey);
        if (recordHotkey>0)
            this.list.splice(this.list.length-recordHotkey, recordHotkey);
        let topHotkey = this.checkHotkey(this.settings.topHotkey);
        if (topHotkey>0)
            this.list.splice(this.list.length-topHotkey, topHotkey);
      }
    };
    this.load(true);
  }

  private checkHotkey(hotkey?: KeyCombination) {
    if (hotkey && hotkey.key) {
      let keyCount = 0;
      if (hotkey.key) keyCount++;
      if (hotkey.ctrlKey) keyCount++;
      if (hotkey.altKey) keyCount++;
      if (hotkey.shiftKey) keyCount++;
      if (this.list.length >= keyCount) {
        let key = "";
        let ctrlKey = false;
        let altKey = false;
        let shiftKey = false;
        let index = this.list.length - 1;
        let keyIndex = -1;
        let direction = null;
        for (let i = 0; i < keyCount; i++)
          if ((this.list[index - i].keyName + "").indexOf("Control") >= 0 &&
            (direction == null || this.list[index - i].eventType == direction)){
            ctrlKey = true;
            direction = this.list[index-i].eventType;
            if (keyIndex < index - i)
                keyIndex = index-i;
          }
          else if ((this.list[index - i].keyName + "").indexOf("Alt") >= 0 &&  
            (direction == null || this.list[index - i].eventType == direction)){
            altKey = true;
            direction = this.list[index-i].eventType;
            if (keyIndex < index - i)
                keyIndex = index-i;
          }
          else if ((this.list[index - i].keyName + "").indexOf("Shift") >= 0 &&  
            (direction == null || this.list[index - i].eventType == direction)){
            shiftKey = true;
            direction = this.list[index-i].eventType;
            if (keyIndex < index - i)
                keyIndex = index-i;
          }
          else if (index-i > keyIndex || (direction ==null || this.list[index - i].eventType == direction)) key = this.list[index - i].keyName;
        if (
          key == hotkey.key &&
          ctrlKey == hotkey.ctrlKey &&
          altKey == hotkey.altKey &&
          shiftKey == hotkey.shiftKey
        )
        if (direction == null || direction != ScriptType.KEY_PRESS)
            return keyCount;
      }
    }
    return 0;
  }

  @Watch("playerState", { deep: true })
  onPlayerStateChanged() {
    if (this.playerState.play) {
      if (!this.list || this.list.length == 0) {
        this.playerState.play = false;
        this.setPlayerState(this.playerState);
        this.$q.dialog({
          message:
            this.lang.message_execute_failed || "Cannot perform this action."
        });
      }
      if (!this.isPlaying) {
        this.isPlaying = true;
        this.playerState.index = 0;
        this.playerState.count = 0;
        this.playScript();
      }
    } else {
      if (this.isPlaying) {
        this.isPlaying = false;
      }
    }
  }

  playScript() {
    if (this.isPlaying) {
      if (!this.playerState.count) this.playerState.count = 0;
      if (this.playerState.index == 0) {
        this.playerState.count += 1;
        this.setPlayerState(this.playerState);
      }
      if (this.playerState.index != null) {
        const script = this.list[this.playerState.index];
        setTimeout(() => {
          if (this.isPlaying) {
            this.scriptTable.scrollTo(this.playerState.index || 0);
            let pidList = [];
            if (
              this.playerState.targetPid &&
              this.playerState.targetPid.length > 0
            ) {
              this.playerState.targetPid.forEach(item =>
                pidList.push(item.pid)
              );
            } else pidList.push(0);
            ScriptService.play(pidList, script).finally(() => {
              if (this.playerState.index != null) {
                this.playerState.index += 1;
                if (this.playerState.index >= this.list.length)
                  this.playerState.index = 0;
                this.playScript();
              }
            });
          }
        }, (script.timeOffset || 0) / (this.playerState.speed || 1));
      }
    }
  }

  get currentIndex() {
    return this.playerState.index;
  }

  load(reload: boolean = false) {
    this.setListData(this.script as ScriptItem[]);
  }

  deleteAll() {
    this.$q
      .dialog({
        cancel: {
          label: this.lang ? this.lang.no : "No",
          flat: true
        },
        ok: {
          label: this.lang ? this.lang.yes : "Yes"
        },
        title: this.lang ? this.lang.confirm_delete_title : "Delete",
        message: this.lang
          ? this.lang.confirm_delete
          : "Are you sure want to delete all selected item(s)?"
      })
      .onOk(() => {
        if (this.selectedItems) {
          this.selectedItems.forEach(item => {
            const index: number = this.list.findIndex(x => x.id == item);
            this.list.splice(index, 1);
          });
          this.selectedItems = [];
        }
      });
  }

  get isCheckedAll() {
    return this.selectedItems.length == this.list.length;
  }

  get isActiveAll() {
    return this.list.filter(x => x.active).length == this.list.length;
  }

  get isDisableAll() {
    return this.list.filter(x => !x.active).length == this.list.length;
  }

  get isManipulatedAll() {
    return this.list.filter(x => x.sendInput).length == this.list.length;
  }

  toggleCheckAll() {
    if (this.isCheckedAll)
      this.selectedItems.splice(0, this.selectedItems.length);
    else this.selectedItems.splice(0, this.selectedItems.length, ...this.list);
  }

  toggleActiveAll() {
    const check = this.isActiveAll;
    for (let i = 0; i < this.list.length; i++) this.list[i].active = !check;
  }
  toggleManipulatedAll() {
    const check = this.isManipulatedAll;
    for (let i = 0; i < this.list.length; i++)
      if (this.list[i].active) this.list[i].sendInput = !check;
  }

  isDirty(row: ScriptItem) {
    let index = this.list.findIndex(x => x == row);
    if (this.script && index >= 0) {
      if (index >= this.script.length) return true;
      return !ScriptItem.compare(this.script[index], row);
    }
    return true;
  }

  get isListDirty() {
    if (this.script && this.list && this.script.length != this.list.length)
      return true;
    if (this.script && this.list && !!this.list.length) {
      if (this.script.length != this.list.length) return true;
      for (let i = 0; i < this.script.length; i++) {
        if (
          this.script[i] == undefined ||
          !ScriptItem.compare(this.list[i], this.script[i])
        )
          return true;
      }
    }
    return false;
  }

  get orderErrorMessage() {
    return this.lang
      ? this.lang.error_orderOutOfRange
      : "Invalid order number.";
  }

  get selectionType() {
    return this.disabled ? "single" : "multiple";
  }

  get typeList() {
    if (this.lang) {
      let result: any[] = [];
      Object.keys(ScriptType).forEach(key => {
        let type = key;
        result.push({
          label: this.lang && this.lang[type] ? this.lang[type] : type,
          value: type
        });
      });
      return result;
    }
    return [];
  }

  donothing() {}

  get randomSample(): ScriptItem[] {
    const list: ScriptItem[] = [];
    const num = 2;
    for (let i = 0; i < num; i++)
      list.push({
        id: 0,
        index: i + 1,
        eventType: ScriptType.MOUSE_UP,
        keyName: i,
        timeOffset: i
      });
    return list;
  }

  private setIndex(data: ScriptItem[]) {
    for (let i = 0; i < data.length; i++) {
      data[i].id = i;
      data[i].index = i + 1;
    }
  }

  setListData(data: ScriptItem[]) {
    // if (this.table){
    this.setScript(data);
    this.setIndex(data);
    this.selectedItems = [];
    this.list = JSON.parse(JSON.stringify(data));
    // }
  }

  @Watch("script")
  onScriptChanged() {
    if (this.script) this.setListData(this.script);
  }

  save() {
    if (this.isListDirty) {
      let list = this.list.sort((a, b) =>
        a.index && b.index ? (a.index > b.index ? 1 : -1) : 0
      );

      this.setListData(list);
    }
  }

  push(item: ScriptItem | undefined = undefined) {
    if (item) this.list.push(item);
    else
      this.list.push({
        id: new Date().getTime(),
        index: this.list.length + 1,
        active: true,
        sendInput: false,
        coord: { x: 0, y: 0 }
      });
  }

  edit(item: ScriptItem) {
    this.editModal.open(item);
  }

  sort(model: ScriptItem) {
    var dIndex = this.list.findIndex(x => x.id == model.id);
    if (dIndex >= 0) {
      this.list.splice(dIndex, 1);
      this.list.push({ ...model });
    }
    this.list = this.list.sort((a, b) =>
      a.index && b.index ? (a.index > b.index ? 1 : -1) : 0
    );
  }

  undo() {
    this.$q
      .dialog({
        title: this.lang ? this.lang.confirm_undo_title : "Undo",
        message: this.lang
          ? this.lang.confirm_undo
          : "Are you sure want to undo all changes?",
        cancel: {
          label: this.lang ? this.lang.no : "No",
          flat: true
        },
        ok: {
          label: this.lang ? this.lang.yes : "Yes"
        }
      })
      .onOk(() => {
        this.load();
      });
  }

  onDragEnter(e: any, targetItem: ScriptItem) {
    if (
      e.target.draggable &&
      e.target.className.indexOf("script-item") >= 0 &&
      !targetItem.clear &&
      this.dragItem &&
      this.dragItem != targetItem
    ) {
      // e.target.classList.add("drag-enter");
      const clear = this.list.findIndex(x => x.clear);
      const dIndex = this.list.findIndex(x => x == this.dragItem);
      const nIndex = this.list.findIndex(x => x == targetItem);
      if (dIndex >= 0) this.list.splice(dIndex, 1);
      if (clear < 0) {
        this.list.splice(nIndex, 0, { id: -1, clear: true });
      } else {
        const clearItem = this.list.splice(clear, 1)[0];
        this.list.splice(nIndex, 0, clearItem);
      }
      this.lastDragIndex = nIndex;
      // this.list.splice(nIndex,0, this.dragItem);
    }
  }
  onDrop(e: any) {
    setTimeout(() => {
      let clear = this.list.findIndex(x => x.clear);
      if (clear >= 0) {
        this.list.splice(clear, 1);
      } else clear = this.lastDragIndex;
      let dIndex = this.list.findIndex(x => x == this.dragItem);
      if (this.dragItem && dIndex < 0)
        this.list.splice(clear, 0, this.dragItem);
    }, 0);
  }
  onDragStart(e: any, item: ScriptItem) {
    if (!item.clear) {
      e.dataTransfer.dropEffect = "move";
      this.dragItem = item;
      this.list;
    }
  }

  isEditableInput(type: ScriptType) {
    return (
      type == ScriptType.ENTER_TEXT ||
      type == ScriptType.ENTER_SECRET ||
      type == ScriptType.RANDOM_TEXT
    );
  }

  log(item: any) {
    console.log(item);
  }
  mouseValue(item: ScriptItem) {
    if (
      item.eventType &&
      this.getScriptType(item.eventType) == "mouse" &&
      item.coord
    )
      return `${item.keyName || ""}[${item.coord.x || 0}:${item.coord.y || 0}]`;
    else return item.keyName;
  }
}
